import React, { useState, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'

import Animate from 'rc-animate'

import { Layout, Row, Col, Button, DownloadOutlined, Collapse } from 'antd'

import config from '../config.json'
const { Header, Footer, Sider, Content } = Layout

const { Panel } = Collapse

function Teams() {
	const { id, core = null } = useParams()
	const [state, setState] = useState({
		team: config.teams.find((t) => t.name === id),
		selectedCore:
			core !== null && id
				? { core, values: config.teams.find((t) => t.name === id)[core] }
				: null,
		loaded: false,
	})

	useEffect(() => {
		setState((state) => ({
			...state,
			team: config.teams.find((t) => t.name === id),
			selectedCore:
				core !== null && id
					? { core, values: config.teams.find((t) => t.name === id)[core] }
					: null,
		}))
	}, [id, core])

	useEffect(() => {
		setTimeout(
			() =>
				setState((state) => ({
					...state,
					loaded: true,
				})),
			200
		)
	}, [])

	console.log(state.selectedCore)
	return (
		<Animate component="" transitionName="fade">
			{state.loaded ? (
				<Layout>
					<Content>
						<Row>
							<Col span={4}>
								<Row
									justify="center"
									align="middle"
									style={{ width: 200, height: 200 }}
								>
									<Row
										justify="center"
										align="middle"
										style={{
											width: 150,
											height: 150,
											backgroundColor:
												core !== null && core === 'values'
													? '#f96302'
													: core === null
													? '#f96302'
													: 'grey',
											borderRadius: '50%',
											border: '2px solid black',
										}}
									>
										<Col>
											<Link
												to={'/team/' + id + '/values'}
												style={{ fontSize: 20 }}
											>
												Values
											</Link>
										</Col>
									</Row>
								</Row>
								<Row
									justify="center"
									align="middle"
									style={{ width: 200, height: 200 }}
								>
									<Row
										justify="center"
										align="middle"
										style={{
											width: 150,
											height: 150,
											backgroundColor:
												core !== null && core === 'services'
													? '#f96302'
													: core === null
													? '#f96302'
													: 'grey',
											borderRadius: '50%',
											border: '2px solid black',
										}}
									>
										<Col>
											<Link
												to={'/team/' + id + '/services'}
												style={{ fontSize: 20 }}
											>
												Services
											</Link>
										</Col>
									</Row>
								</Row>
								<Row
									justify="center"
									align="middle"
									style={{ width: 200, height: 200 }}
								>
									<Row
										justify="center"
										align="middle"
										style={{
											width: 150,
											height: 150,
											backgroundColor:
												core !== null && core === 'work'
													? '#f96302'
													: core === null
													? '#f96302'
													: 'grey',
											borderRadius: '50%',
											border: '2px solid black',
										}}
									>
										<Col>
											<Link
												to={'/team/' + id + '/work'}
												style={{ fontSize: 20 }}
											>
												Work
											</Link>
										</Col>
									</Row>
								</Row>
							</Col>
							<Col span={20}>
								<Row style={{ padding: 30 }}>
									<Col span={24}>
										{state.selectedCore && state.selectedCore.values
											? state.selectedCore.values.map((item, i) => (
													<Collapse
														onChange={() => {}}
														key={i}
														style={{ margin: 20 }}
													>
														<Panel
															showArrow={false}
															header={item.title}
															style={{ fontSize: 20, background: '#f96302' }}
														>
															<Row>
																<Col span={24} style={{ padding: 20 }}>
																	<h4>{item.description}</h4>
																</Col>
															</Row>

															<Row>
																<Col span={8}>
																	<Row>
																		<Col>
																			<h4>General</h4>
																			<ul>
																				{item.information.general.map(
																					(link) => (
																						<li>
																							<a href={link.url}>{link.name}</a>
																						</li>
																					)
																				)}
																			</ul>
																		</Col>
																	</Row>
																</Col>
																<Col span={8}>
																	<Row>
																		<Col>
																			<h4>Documentation</h4>
																			<ul>
																				{item.information.documentation.map(
																					(link) => (
																						<li>
																							<a href={link.url}>{link.name}</a>
																						</li>
																					)
																				)}
																			</ul>
																		</Col>
																	</Row>
																</Col>
																<Col span={8}>
																	<Row>
																		<Col>
																			<h4>Contacts</h4>
																			<ul>
																				{item.information.contact.map(
																					(link) => (
																						<li>
																							<a href={link.url}>{link.name}</a>
																						</li>
																					)
																				)}
																			</ul>
																		</Col>
																	</Row>
																</Col>
															</Row>
														</Panel>
													</Collapse>
											  ))
											: null}
									</Col>
								</Row>
							</Col>
						</Row>
					</Content>
					<Footer>
						<Row justify="end">
							<Col span={24}>
								<Link to="/">Back to Intro</Link>
							</Col>
						</Row>
					</Footer>
				</Layout>
			) : null}
		</Animate>
	)
}

export default Teams
