import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Layout } from 'antd'

import './App.scss'
import Intro from './components/Intro'
import Team from './components/Team'

function App() {
	return (
		<Layout>
			<Switch>
				<Route exact path="/">
					<Intro />
				</Route>
				<Route path="/team/:id/:core?">
					<Team />
				</Route>
			</Switch>
		</Layout>
	)
}

export default App
